import React, { Component } from 'react';
import { connect } from "react-redux";
import { NavLink, Link } from 'react-router-dom'
import { baseUrl } from '../../Assets/AssetsHelper';
import SVG from 'react-inlinesvg';
import { Collapse } from 'react-bootstrap';
import { useLocation } from '../../Services/Utils/utils';
import { resetFilter } from '../../Services/Actions/assetsActions'
import { IS_LIVE } from '../../Services/Api'
import { uploadModalClick } from '../../Services/Actions/homeActions';
import Swal from 'sweetalert2';
import { manageAsideMenuPermissions } from '../../Services/Utils/utils';

class AsideMenu extends Component {
    constructor() {
        super();
        this.state = {
            support: false
        };
    }
    submenuDropdown = (val) => {
        if (val === 'collections') {
            this.setState({
                support: false
            })
        } else {
            this.setState({
                support: !this.state.support
            })
        }
    }
    closeAsideToggle = (val, assetsStatus) => {
        if (val === 'main') {
            this.setState({
                support: false
            })
        } else if (val === 'child-menu') {
            this.setState({
                support: false
            })
        }
        var rootEle = document.getElementById('ugc-suit-main');
        if (rootEle) {
            rootEle.classList.remove('aside-on');
        }
        if (assetsStatus && String(window.location.href).includes("assets")) {
            this.props.resetFilter()
            const overlay = document.querySelector(".offcanvas-backdrop")
            if (overlay) {
                overlay.click()
            }
        }
    }
    menuActiveClass = (menuLink) => {

        try {
            if (window.location.href && String(window.location.href).includes(menuLink)) return 'active'
            else return ''
        }
        catch (ex) {
            return ''
        }
    }

    managePermission = (checkPermission) => {
        const { userData, history } = this.props
        if (userData.collaborator != '' && userData.collaborator.permissions != "") {
            const isBlocked = manageAsideMenuPermissions(checkPermission, userData)
            if (isBlocked) this.permissionError()
            else {
                if (checkPermission == 'manage-connection') {
                    this.closeAsideToggle('main')
                    userData.collaborator.permissions.networkManager.manageConnection ? history("/manage-connection") : history("/social-account")
                }
                else if (checkPermission == 'assets') {
                    this.closeAsideToggle('main', true)
                    history("/assets")
                }
                else history(`/${checkPermission}`)
            }
        }
        else {
            this.closeAsideToggle('main', checkPermission == 'assets' ? true : null)
            history(`/${checkPermission}`)
        }
    }

    permissionError = () => {
        Swal.fire({
            title: 'Oops!',
            text: "It seems you're not currently authorized to use this module. Please reach out to your Administrator to request access.",
            icon: 'error',
        })
    }


    render() {
        const { support } = this.state;
        const { userData } = this.props
        const { menuActiveClass } = this
        return <div className="sidebar-menu" onClick={event => this.props.uploadModalClick(false)}>
            <ul className="sidebar-menu-list list-unstyled mb-0 p-0">
                <li className="sidebar-menu-items">
                    <Link to="/home" className={`nav-menu-item block-center flex-lg-column text-decoration-none ${menuActiveClass("home")}`} activeclassname="active" onClick={() => this.closeAsideToggle('main')}>
                        <span className="menu-icon me-2 me-lg-0 mb-0 mb-lg-2"><i className="ri-home-smile-line"></i></span>
                        <span className="menu-text">Dashboard</span>
                    </Link>
                </li>
                <li className="sidebar-menu-items">
                    <div className={`nav-menu-item block-center flex-lg-column text-decoration-none ${menuActiveClass("assets")}`} activeclassname="active" onClick={() => this.managePermission('assets')} >
                        <span className="menu-icon me-2 me-lg-0 mb-0 mb-lg-2"><i className="ri-image-line"></i></span>
                        <span className="menu-text">UGC Library</span>
                    </div>
                </li>
                <li className="sidebar-menu-items">
                    <div className={`nav-menu-item block-center flex-lg-column text-decoration-none ${menuActiveClass("manage-connection")} ${menuActiveClass("social-account")}`} activeclassname="active" onClick={() => this.managePermission('manage-connection')}>
                        <span className="menu-icon me-2 me-lg-0 mb-0 mb-lg-2">
                            <SVG src={baseUrl('media/icons/svg-icons/connection.svg')} />
                        </span>
                        <span className="menu-text">Network Manager</span>
                    </div>
                </li>
                <li className="sidebar-menu-items">
                    <div className={`nav-menu-item block-center flex-lg-column text-decoration-none ${menuActiveClass("template")}`} activeclassname="active" onClick={() => this.managePermission('template')}>
                        <span className="menu-icon me-2 me-lg-0 mb-0 mb-lg-2"><i className="ri-layout-4-line"></i></span>
                        <span className="menu-text">Style Studio</span>
                    </div>
                </li>
                <li className="sidebar-menu-items">
                    <div className={`nav-menu-item block-center flex-lg-column text-decoration-none ${menuActiveClass("rule")}`} onClick={() => this.managePermission('rule')}>
                        <span className="menu-icon me-2 me-lg-0 mb-0 mb-lg-2"><i className="ri-draft-line"></i></span>
                        <span className="menu-text">AutoPilot</span>
                    </div >
                </li>
                <li className="sidebar-menu-items">
                    <div className={`nav-menu-item block-center flex-lg-column text-decoration-none ${menuActiveClass("influencer")}`} onClick={() => this.managePermission('creators')}>
                        <span className="menu-icon me-2 me-lg-0 mb-0 mb-lg-2"><i className="ri-megaphone-line"></i></span>
                        <span className="menu-text">Creators</span>
                    </div>
                </li>
            </ul>
            <hr className="w-75 d-lg-flex d-none" style={{ margin: '2vh auto' }} />
            <ul className="sidebar-menu-list list-unstyled mb-0 p-0">
                <li className="sidebar-menu-items">
                    <div className={`nav-menu-item block-center flex-lg-column text-decoration-none ${menuActiveClass("analytics")}`} onClick={() => this.managePermission('analytics')}>
                        <span className="menu-icon me-2 me-lg-0 mb-0 mb-lg-2"><i className="ri-line-chart-line"></i></span>
                        <span className="menu-text">Insights</span>
                    </div >
                </li>
                <li className={`sidebar-menu-items ${support ? 'active' : ''}`}>
                    <span className="menu-toggle nav-menu-item block-center flex-lg-column text-decoration-none justify-content-between" onClick={() => this.submenuDropdown('support')}>
                        <span className="menu-icon me-2 me-lg-0 mb-0 mb-lg-2"><i className="ri-question-line"></i></span>
                        <span className="menu-text flex-grow-1">Help Center</span>
                        <span className="menu-arrow"></span>
                    </span>
                    <div className="menu-submenu">
                        <ul className="submenu-nav list-unstyled">
                            <li className="submenu-nav-item">
                                <Link className="nav-menu-item block-center text-decoration-none"
                                    //onClick={() => this.closeAsideToggle('child-menu')} 
                                    onClick={window.Intercom ? (e) => { window.Intercom('show') } : () => { }}>
                                    <span className="menu-icon me-2"><i className="ri-chat-3-line"></i></span>
                                    <span className="menu-text">Chat</span>
                                </Link >
                            </li>
                            <li className="submenu-nav-item">
                                <a href="tel:+1628900-3005" className="nav-menu-item block-center text-decoration-none">
                                    <span className="menu-icon me-2"><i className="ri-phone-line"></i></span>
                                    <span className="menu-text">Call</span>
                                </a>
                            </li>
                            <li className="submenu-nav-item">
                                <a href="mailto:support@taggbox.com" className="nav-menu-item block-center text-decoration-none">
                                    <span className="menu-icon me-2"><i className="ri-mail-line"></i></span>
                                    <span className="menu-text">Email</span>
                                </a>
                            </li>
                        </ul>
                    </div>
                </li>
                <li className="sidebar-menu-items">
                    <div className={`nav-menu-item block-center flex-lg-column text-decoration-none ${menuActiveClass("trash")}`} activeclassname="active" onClick={() => this.managePermission('trash')}>
                        <span className="menu-icon me-2 me-lg-0 mb-0 mb-lg-2"><i className="ri-delete-bin-line"></i></span>
                        <span className="menu-text">Trash</span>
                    </div>
                </li>
            </ul>
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        userData: state.authenticated.userData,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        resetFilter: () => dispatch(resetFilter()),
        uploadModalClick: (status) => dispatch(uploadModalClick(status))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(AsideMenu);